<template>
    <!--<Collapse v-model="value1">-->
        <!--<Panel v-for="i in tables">-->
            <!--{{tables[i].name}}-->
            <!--<p slot="content">史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。</p>-->
        <!--</Panel>-->
    <!--</Collapse>-->
    <div>
        <Collapse v-model="value1">
            <Panel v-for="i in tables['data']">
                {{tables.data[i].Name}}
                <p slot="content">史s蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。</p>
            </Panel>
        </Collapse>
    </div>
    <!--<p v-for="i in tables">32432443</p>-->
</template>

<script>
  export default {
    name: 'nav.vue',
    data:()=>{
      return{
        value1:'1',
        tables:{}
      }
    },
    methods:{},
    created () {
      this.$http.get("/console/nav/get").then(function (data){
        data=data.body;

        for (let i in data) {
          this.$set(this.tables,i,{ //根据nav有多少大类 初始化tables有多少
            name:data[i]['name'],
            columns: [
              {
                title: '名称',
                key: 'Name'
              },
              {
                title: '链接',
                key: 'Url'
              },
              {
                title: '操作',
                key: 'Option',
                render: (h, params) => {
                  return h('div', [
                    h('Button', {
                      props: {
                        type: 'primary',
                        size: 'small'
                      },
                      on: {
                        click: () => {
                          this.change(params.index)
                        }
                      }
                    }, '修改'),
                    h('Button', {
                      props: {
                        type: 'error',
                        size: 'small'
                      },
                      on: {
                        click: () => {
                          this.delete(params.index)
                        }
                      }
                    }, '删除')
                  ])
                }
              }
            ],
            data: []
          })
          let n = 0;
          for(let j in data[i]["menu"]){//由每个类中menu有多少子类 初始化多少行
            this.$set(this.tables[i].data, n, {
              Name: data[i]['menu'][j]['name'],
              Url: data[i]['menu'][j]['url']
            })
            n++
          }
        }
        console.log(this.tables)
      },function (err) {
        this.$Modal.error({
          title: '警告',
          content: '服务器发生错误'
        })
      });
    }
  }
</script>

